<template>
    <div class="project-list">
        <transition-group name="project-list" tag="div" :css="false" appear @before-appear="beforeProjectItemAppear" @appear="projectItemAppear" v-masonry column-width=".project-item-masonry-sizer" item-selector=".project-item" percent-position="true">
            <div class="project-item-masonry-sizer" key="masonry-sizer"></div>
            <div v-for="(project, index) of projects" :key="project.title" :data-index="index" v-masonry-tile class="project-item" :class="{ 'project-item-span': project.span }">
                <project-view :project="project"></project-view>
            </div>
        </transition-group>
    </div>
</template>

<style lang="less" scoped>
    .project-list {
        @media (min-width: 1280px) {
            max-width: calc(1024px + 2 * 24px);
            margin-left: auto;
            margin-right: auto;
        }
        padding: 12px;
        @media (max-width: 599px) {
            padding: 8px;
        }
    }
    .project-item-masonry-sizer, .project-item {
        width: 33.33%;
        @media (max-width: 959px) and (min-width: 600px) {
            width: 50%;
        }
        @media (max-width: 599px) {
            width: 100%;
        }
        &.project-item-span {
            width: 66.66%;
            @media (max-width: 959px) and (min-width: 600px) {
                width: 50%;
            }
            @media (max-width: 599px) {
                width: 100%;
            }
        }
    }
    .project-item {
        padding: 12px;
        @media (max-width: 599px) {
            padding: 8px;
        }
    }
    .project-item-before-appear {
        opacity: 0;
        transform: translateY(100%);
    }
    .project-item-appear-active {
        transition: opacity cubic-bezier(0, 0, 0.2, 1) 0.4s, transform cubic-bezier(0, 0, 0.2, 1) 0.4s;
    }
</style>

<script>
import kebabCase from 'lodash.kebabcase';

import data from '../data';
import ProjectView from "./ProjectView.vue";

export default {
    name: 'project-list-view',
    components: {
        ProjectView,
    },
    props: {
        tag: {
            type: String,
            default: null
        }
    },
    computed: {
        projects() {
            return this.tag ? data.projects.filter(project => project.tags.some(tag => kebabCase(tag) === this.tag)) : data.projects;
        }
    },
    methods: {
        beforeProjectItemAppear(el) {
            el.classList.add('project-item-appear-active', 'project-item-before-appear');
        },
        projectItemAppear(el, done) {
            setTimeout(() => {
                el.classList.remove('project-item-before-appear');
                setTimeout(() => {
                    el.classList.remove('project-item-appear-active');
                    done();
                }, 400);
            }, el.dataset.index * 100);
        }
    }/*,
    watch: {
        $route(to, from) {
            if (to.path === '/') {
                this.tag = null;
            } else {
                this.tag = startCase(to.params.tag);
            }
        }
    }*/
}
</script>



