'use strict';

import camelCase from 'lodash.camelcase';
import ImagesLoaded from 'imagesloaded';
import Masonry from 'masonry-layout';
import Vue from 'vue';

const NumberOrString = {};
const optionMap = {
    itemSelector: String,
    columnWidth: NumberOrString,
    gutter: Number,
    horizontalOrder: Boolean,
    percentPosition: Boolean,
    stamp: String,
    fitWidth: Boolean,
    originLeft: Boolean,
    originTop: Boolean,
    containerStyle: String,
    transitionDuration: String,
    stagger: String,
    resize: Boolean,
    initLayout: Boolean
};

const EVENT_ADD = 'vue-masonry.item-added';
const EVENT_REMOVE = 'vue-masonry.item-removed';
const EVENT_IMAGE_LOADED = 'vue-masonry.image-loaded';

const Events = new Vue({});

const VueMasonryPlugin = {

    install(Vue, options) {

        Vue.directive('masonry', {
            inserted(el) {
                if (!Masonry) {
                    throw new Error('Masonry plugin is not defined. Please check it\'s connected and parsed correctly.');
                }
                const options = {};
                for (const attribute of Array.from(el.attributes)) {
                    const name = camelCase(attribute.name);
                    let type = optionMap[name];
                    if (type === NumberOrString) {
                        type = isNaN(attribute.value) ? String : Number;
                    }
                    switch (type) {
                        case Boolean:
                            options[name] = attribute.value === 'true';
                            break;
                        case Number:
                            options[name] = Number(attribute.value);
                            break;
                        case String:
                            options[name] = attribute.value;
                            break;
                    }
                }
                const masonry = new Masonry(el, options);
                const masonryDraw = () => {
                    masonry.reloadItems();
                    masonry.layout();
                };
                Vue.nextTick(masonryDraw);
                Events.$on(EVENT_ADD, masonryDraw);
                Events.$on(EVENT_REMOVE, masonryDraw);
                Events.$on(EVENT_IMAGE_LOADED, masonryDraw);
            }
        });

        Vue.directive('masonryTile', {
            inserted(el) {
                Events.$emit(EVENT_ADD);
                new ImagesLoaded(el, () => {
                    Events.$emit(EVENT_IMAGE_LOADED)
                });
            },
            beforeDestroy(el) {
                Events.$emit(EVENT_REMOVE)
            }
        });

        Vue.prototype.$redrawVueMasonry = function () {
            Events.$emit(EVENT_ADD)
        };
    }
};

export default VueMasonryPlugin;



