<template>
    <a :href="project.url" target="_blank" class="project-link">
        <md-card md-with-hover>
            <md-card-media :style="{ 'padding-bottom': (100 * project.image.height / project.image.width) + '%' }">
                <img :src="'assets/' + project.image.url">
            </md-card-media>
            <md-card-header>
                <div class="md-title">{{project.title}}</div>
                <div class="md-subhead">{{project.description}}</div>
            </md-card-header>
            <md-ink-ripple></md-ink-ripple>
        </md-card>
    </a>
</template>

<script>
export default {
    name: 'project-view',
    props: {
        project: Object
    }
}
</script>

<style lang="less" scoped>
    .project-link {
        display: block;
        color: inherit;
        outline: none;
        &:hover {
            text-decoration: none !important;
        }
    }
    .md-card-media {
        position: relative;
        img {
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            width: 100%;
            height: 100%;
        }
    }
</style>



