<template>
  <div id="app">
      <div ref="appBar" class="appbar">
          <div>
              <md-toolbar>
                  <h1 class="md-title">My Projects</h1>
              </md-toolbar>
              <!-- Using @change because the header-item slot of md-tabs is broken so we cannot use router-link. -->
              <md-tabs ref="tabs" :md-dynamic-height="false" @change="onTabChange">
                  <md-tab md-label="All"></md-tab>
                  <md-tab v-for="tag of tags" :key="tag" :md-label="tag"></md-tab>
              </md-tabs>
              <a href="//resume.zhanghai.me" title="Resume" target="_blank" class="fab">
                  <md-button class="md-fab">
                      <md-icon>portrait</md-icon>
                  </md-button>
              </a>
          </div>
      </div>
      <!-- Using key to force component creation, because masonry's transition cannot work properly with addition or removal. -->
      <router-view ref="content" :key="$route.path" class="content"></router-view>
  </div>
</template>

<style>
* {
    box-sizing: border-box;
}
</style>
<style lang="less" scoped>
.appbar {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    min-height: 104px;
    max-height: 256px;
    height: 256px;
    @media (max-width: 1279px) {
        max-height: 104px;
        height: 104px;
    }
    background: #039be5;
    // Looks weird with tabs
    //transition: box-shadow ease-in-out 0.2s;
    > div {
        position: relative;
        @media (min-width: 1280px) {
            max-width: 1024px;
            margin-left: auto;
            margin-right: auto;
        }
        height: 100%;
    }
    .md-toolbar {
        @media (min-width: 1280px) {
            margin-left: -16px;
            margin-right: -16px;
        }
        @media (max-width: 1279px) and (min-width: 600px) {
            margin-left: 8px;
            margin-right: 8px;
        }
        min-height: 56px;
        background: none;
        color: white;
        z-index: 0;
        h1 {
            font-weight: 500;
        }
    }
}
.content {
    margin-top: 256px - (256px - 104px) / 2 - 24px;
    @media (max-width: 1279px) {
        margin-top: 104px;
    }
    margin-bottom: 24px + 56px;
    @media (max-width: 599px) {
        margin-bottom: 16px + 56px;
    }
}
.fab {
    display: block;
    position: fixed;
    right: 24px;
    bottom: 24px;
    @media (max-width: 599px) {
        right: 16px;
        bottom: 16px;
    }
    @media (min-width: 1280px) {
        position: absolute;
        right: -(24px + 56px);
        bottom: 0;
        transform: translateY(50%);
    }
    z-index: 12;
    .md-fab {
        margin: 0;
        box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.14), 0 1px 18px 0 rgba(0, 0, 0, 0.12), 0 3px 5px -1px rgba(0, 0, 0, 0.2) !important;
        &:active {
                box-shadow: 0px 7px 8px -4px rgba(0, 0, 0, 0.2), 0px 12px 17px 2px rgba(0, 0, 0, 0.14), 0px 5px 22px 4px rgba(0, 0, 0, 0.12) !important;
        }
    }
}
</style>
<style lang="less">
    .appbar {
        .md-tabs {
            @media (min-width: 1280px) {
                margin-left: -22px;
                margin-right: -22px;
            }
            .md-tabs-navigation {
                background: none !important;
                .md-tab-header.md-active {
                    color: white !important;
                }
                .md-tab-indicator {
                    background: white !important;
                }
                .md-tab-header-navigation-button {
                    background: none !important;
                }
            }
            .md-tabs-content {
                display: none;
            }
        }
    }
</style>

<script>
import kebabCase from 'lodash.kebabcase';

import data from './data';
import router from './router';

export default {
    name: 'app',
    data() {
        return {
            tags: data.tags,
            onScrollListener: () => window.requestAnimationFrame(this.onScroll)
        };
    },
    mounted() {
        // Ripples are messy with touch.
        const hasTouch = 'ontouchstart' in window || navigator.maxTouchPoints;
        this.$material.inkRipple = !hasTouch;
        this.onScrollListener();
        window.addEventListener('scroll', this.onScrollListener, false);
        window.addEventListener('resize', this.onScrollListener, false);
    },
    beforeDestroy() {
        window.removeEventListener('scroll', this.onScrollListener, false)
        window.removeEventListener('resize', this.onScrollListener, false);
    },
    methods: {
        onScroll() {
            const { $refs: { appBar, content: { $el: content } } } = this;
            const appBarStyle = window.getComputedStyle(appBar, null);
            const contentStyle = window.getComputedStyle(content, null);
            const getPropertyValue = (style, property) => {
                const value = style.getPropertyValue(property);
                if (!value.endsWith('px')) {
                    throw new Error(`${property} must end in 'px'`);
                }
                return Number(value.substring(0, value.length - 2));
            };
            const contentMarginTop = getPropertyValue(contentStyle, 'margin-top');
            const appBarMinHeight = getPropertyValue(appBarStyle, 'min-height');
            const appBarMaxHeight = getPropertyValue(appBarStyle, 'max-height');
            const distance = contentMarginTop - appBarMinHeight;
            const fraction = distance > 0 ? Math.min(window.pageYOffset / distance, 1) : 1;
            appBar.style.height = `${appBarMinHeight + (1 - fraction) * (appBarMaxHeight - appBarMinHeight)}px`;
            if (fraction === 1) {
                appBar.classList.add('md-whiteframe-4dp');
                appBar.style.zIndex = '8';
            } else {
                appBar.classList.remove('md-whiteframe-4dp');
                appBar.style.zIndex = '0';
            }
        },
        onTabChange() {
            const { $refs: { tabs } } = this;
            const tag = tabs.tabList[tabs.activeTab].label;
            let location = kebabCase(tag);
            if (location === 'all') {
                location = '/';
            }
            router.replace(location);
            window.scrollTo(0, 0);
        }
    }
};
</script>



